.game-tab-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 20px;
  background-color: #1a202c; /* dark background */
  color: #f7fafc; /* light text */
}

.character-panel,
.threat-panel {
  width: 20%;
}

.explore-panel {
  width: 60%;
  text-align: center;
}

.combat-cards,
.combat-hands {
  margin-top: 20px;
}
